<template>
    <main id="adminMember">
        <div class="row" style="margin-top:10px;margin-bottom:10px;margin-left:15px;margin-right:15px;">
            <table style="width:100%;">
                <tr>
                    <td rowspan="2" style="width:85px;">
                        <button class="btn btn-success" @click="setVoidOrders"></button>
                    </td>
                    <td style="width:220px;">
                        <button class="btn btn-danger" @click="deleteVoidOrders"></button>
                    </td>
                    <td style="width:220px;">
                        <select class="form-control" v-model="consume.state">
                            <option>--</option>
                            <option></option>
                            <option></option>
                            <option></option>
                        </select>
                    </td>
                    <td style="text-align:right;width:140px;" rowspan="2">
                        <label style="margin-right:5px;">:</label></td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="minUseTime" type="datetime" placeholder=""
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMinUseTime">
                        </el-date-picker>
                    </td>
                    <td style="text-align:right;" rowspan="2">
                        <label style="margin-right:5px;">:</label>
                    </td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="minCreated" type="datetime" placeholder=""
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMinCreated">
                        </el-date-picker>
                    </td>
                    <td style="width:600px;" rowspan="2">
                        <div class="input-group">
                            <input type="text" class="form-control" placeholder="//////"
                                v-model="condition">
                            <span class="input-group-btn">
                                <button class="btn btn-info" type="button" @click="getConsumes('find')"></button>
                            </span>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td>
                        <button class="btn btn-warning" @click="batchDelConsumes"></button>
                    </td>
                    <td>
                        <select class="form-control" v-model="yacht.state">
                            <option>--</option>
                            <option></option>
                            <option></option>
                        </select>
                    </td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="maxUseTime" type="datetime" placeholder=""
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMaxUseTime">
                        </el-date-picker>
                    </td>
                    <td style="text-align:left;">
                        <el-date-picker v-model="maxCreated" type="datetime" placeholder=""
                            format="yyyy-MM-dd HH:mm" value-format="yyyy-MM-dd HH:mm" @change="getMaxCreated">
                        </el-date-picker>
                    </td>
                </tr>
            </table>
        </div>
        <div style="height:594px;">
            <table class="table" style="margin:auto;">
                <thead>
                    <tr>
                        <td>
                            <span class="input-group-addon">
                                <input id="BatchAllCheckbox" type="checkbox" @click="selectBatchDel">
                            </span>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="consume in consumeList" :key="consume.id">
                        <td>
                            <span class="input-group-addon">
                                <input type="checkbox" :value="consume.id" v-model="batchDelList"
                                    :disabled="consume.state == 0||consume.state == 1">
                            </span>
                        </td>
                        <td><a>{{consume.id}}</a></td>
                        <td>{{consume.memberId}}</td>
                        <td>{{consume.tel}}</td>
                        <td>{{consume.name}}</td>
                        <td>{{consume.yacht.id}}</td>
                        <td>{{consume.yachtScheme?consume.yachtScheme.time+'':''}}</td>
                        <td>
                            <span v-if="consume.yacht.state==1">{{consume.useTime|formatDate}}</span>
                            <span v-if="consume.yacht.state==2||consume.yacht.state==3"></span>
                        </td>
                        <td>{{consume.yachtScheme?'':''}}</td>
                        <td>{{consume.state == 1?'':''}}</td>
                        <td><a :href="downLoadOrder+consume.orderId" title="">{{consume.orderId}}</a></td>
                        <td>{{consume.platformorderId?consume.platformorderId:''}}</td>
                        <td>
                            <span v-if="consume.state==1" style="color:green"></span>
                            <span v-if="consume.state==0" style="color:red"></span>
                            <span v-if="consume.state==2" style="color:lightgrey"></span>
                        </td>
                        <td> {{(consume.price/100)|NumFormat}}</td>
                        <td>{{consume.created|formatDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div style="margin-top:2px;">
            <div>{{allCount}}, {{allPage}}</div>
            <nav aria-label="Page navigation">
                <ul class="pagination justify-content-center">
                    <li class="page-item" :class="{disabled: nowPage===1}">
                        <a href="#" aria-label="previous" class="page-link">
                            <span @click="nowPage--; getConsumes();"></span>
                        </a>
                    </li>
                    <li v-for="page in allPage" :key="page" class="page-item" :class="{active: nowPage===page}"><a
                            href="#" class="page-link" @click="nowPage = page; getConsumes();"
                            v-if="page>nowPage-5&&page<nowPage+5">{{page}}</a></li>
                    <li class="page-item" :class="{disabled: nowPage===allPage}">
                        <a href="#" aria-label="Next" class="page-link">
                            <span @click="nowPage++; getConsumes();"></span>
                        </a>
                    </li>
                </ul>
            </nav>
        </div>
    </main>
</template>

<script>
    export default {
        name: 'adminMember',
        data() {
            return {
                consume: {
                    id: "",
                    memberId: "",
                    tel: "",
                    name: "",
                    schemeId: "",
                    useTime: "",
                    typeId: "",
                    orderId: "",
                    platformorderId: "",
                    price: "",
                    state: "--",
                    payMessage: "",
                    created: "",
                    yacht: "",
                    yachtScheme: ""
                },
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    state: "--"
                },
                yachtScheme: {
                    id: "",
                    yachtId: "",
                    time: "",
                    price: ""
                },
                consumeList: [],
                condition: "", //,
                minUseTime: "", //,
                maxUseTime: "", //,
                minCreated: "", //,
                maxCreated: "", //,
                nowPage: 1, //
                allPage: 0, //
                counts: 11, //
                allCount: 0, //
                batchDelList: [] //checkboxid
            }
        },
        methods: {
            getConsumes(button) {
                this.batchDelList = []; //checkbox
                $("#BatchAllCheckbox").prop("checked", false); //

                var state = "";
                if (this.consume.state == "--") {
                    state = "";
                } else if (this.consume.state == "") {
                    state = 1;
                } else if (this.consume.state == "") {
                    state = 0;
                } else if (this.consume.state == "") {
                    state = 2;
                }
                var productType = "";
                if (this.yacht.state == "") {
                    productType = 1;
                } else if (this.yacht.state == "") {
                    productType = 2;
                } else if (this.yacht.state == "--") {
                    productType = "";
                }
                if (button == "find") { // "" , "" ,
                    this.nowPage = 1;
                }
                if (this.minUseTime == null) {
                    this.minUseTime = "";
                }
                if (this.maxUseTime == null) {
                    this.maxUseTime = "";
                }
                if (this.minCreated == null) {
                    this.minCreated = "";
                }
                if (this.maxCreated == null) {
                    this.maxCreated = "";
                }

                this.axios({
                    method: "post",
                    url: "/api/consume/admin/getConsumes.action",
                    data: {
                        condition: this.condition,
                        state,
                        productType,
                        minUseTime: this.minUseTime,
                        maxUseTime: this.maxUseTime,
                        minCreated: this.minCreated,
                        maxCreated: this.maxCreated,
                        page: this.nowPage,
                        counts: this.counts
                    },
                    transformRequest: [function (data) { //post
                        // Do whatever you want to transform the data
                        let ret = '';
                        for (let it in data) {
                            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&';
                        }
                        return ret;
                    }],
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //
                        window.location.href = '/#/admin';
                    } else if (map.code == 200) {
                        var data = JSON.parse(map.data);
                        if (data != null) {
                            var consumeJson = data; //json,json[0]listjson,json[1]
                            this.allCount = consumeJson[1]; //
                            this.allPage = parseInt(this.allCount / (this.counts + 1)) + 1; //
                            if (this.nowPage > this.allPage) { //,,
                                this.nowPage = 1;
                                this.getConsumes();
                            } else {
                                this.consumeList = JSON.parse(consumeJson[0]); //
                            }
                        }
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //
                        window.location.href = '/#/admin';
                    }
                });
            },
            getMinUseTime(val) {
                this.minUseTime = val;
            },
            getMaxUseTime(val) {
                this.maxUseTime = val;
            },
            getMinCreated(val) {
                this.minCreated = val;
            },
            getMaxCreated(val) {
                this.maxCreated = val;
            },
            selectBatchDel() {
                if ($("#BatchAllCheckbox").prop('checked')) {
                    for (var i = 0; i < this.consumeList.length; ++i) {
                        if (this.consumeList[i].state == 2) { //,checkbox
                            this.batchDelList.push(this.consumeList[i].id);
                        }
                    }
                } else {
                    this.batchDelList = [];
                }
            },
            batchDelConsumes() {
                var confi = confirm("?");
                if (confi == true) {
                    this.axios({
                        method: "post",
                        url: "/api/consume/admin/deleteNullConsumes.action",
                        data: this.batchDelList
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) {  //
                            window.location.href = '/#/admin';
                        } else if (map.code == 200) {
                            if (map.data == null) {
                                this.getConsumes("find");
                                this.$emit("showAlert", true, "");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        } else{
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            deleteVoidOrders() {
                var confi = confirm("");
                if (confi == true) {
                    this.axios({
                        url: "/api/consume/admin/deleteVoidOrders.action",
                        method: "get"
                    }).then((response) => {
                        var map = response.data;
                        if(map.code == 401){//
                            window.location.href = '/#/admin';
                        }else if(map.code == 200){
                            if(map.data == null){
                                this.getConsumes();
                                this.$emit("showAlert", true, "");
                            }
                        }else{
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //
                            window.location.href = '/#/admin';
                        }
                    })
                }
            },
            setVoidOrders() {
                var confi = confirm(",");
                if (confi == true) {
                    this.axios({
                        url: "/api/consume/admin/setVoidOrders.action",
                        method: "get"
                    }).then((response) => {
                        var map = response.data;
                        if(map.code == 401){
                            window.location.href = '/#/admin';
                        }else if(map.code == 200){
                            if(map.data == null){
                                this.$emit("showAlert", true, "");
                                this.getConsumes();
                            }else{
                                this.$emit("showAlert", true, map.data);
                            }
                        }else{
                            console.log(map);
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //
                            window.location.href = '/#/admin';
                        }
                    });
                }
            }
        },
        mounted() {
            this.getConsumes();
        },
        filters: {
            formatDate(dataStr) { //
                var time = new Date(dataStr);

                function timeAdd0(str) {
                    if (str < 10) {
                        str = '0' + str;
                    }
                    return str
                }
                var y = time.getFullYear();
                var m = time.getMonth() + 1;
                var d = time.getDate();
                var h = time.getHours();
                var mm = time.getMinutes();
                var s = time.getSeconds();
                return y + '-' + timeAdd0(m) + '-' + timeAdd0(d) + ' ' + timeAdd0(h) + ':' + timeAdd0(mm);
            },
            NumFormat(value) { //
                if (!value) return '0.00';

                /*Number(value).toFixed(0)0.511Nils*/
                var intPart = Number(value) | 0; //
                var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //

                var floatPart = ".00"; //
                value = value.toString();
                var value2Array = value.split(".");

                //=2
                if (value2Array.length == 2) {
                    floatPart = value2Array[1].toString(); //

                    if (floatPart.length == 1) { //0,
                        return intPartFormat + "." + floatPart + '0';
                    } else {
                        return intPartFormat + "." + floatPart;
                    }

                } else {
                    return intPartFormat + floatPart;
                }
            }
        }
    }
</script>

<style scoped>

</style>