<template>
    <div>
        <main id="adminMember">
            <div class="row" style="margin-top:10px;margin-bottom:10px;margin-left:15px;margin-right:15px;">
                <table style="width:100%;white-space:nowrap;">
                    <tr>
                        <td>:</td>
                        <td style="padding-right:55px;">
                            <select class="form-control" v-model="member_c.sex">
                                <option>--</option>
                                <option></option>
                                <option></option>
                            </select>
                        </td>
                        <td>:</td>
                        <td style="padding-right:55px;">
                            <select class="form-control" v-model="member_c.level">
                                <option>--</option>
                                <option></option>
                                <option></option>
                            </select>
                        </td>
                        <td style="width:75px;text-align:right;padding-top:7px;">
                            <label for="exampleInputName2">: </label>
                        </td>
                        <td style="width:105px;">
                            <input type="number" class="form-control" placeholder="" min="0" v-model="minAge"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </td>
                        <td>~</td>
                        <td style="width:105px;">
                            <input type="number" class="form-control" placeholder="" min="0" v-model="maxAge"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </td>
                        <td style="width:600px;padding-left:95px;" rowspan="2">
                            <div class="input-group">
                                <input type="text" class="form-control" placeholder="//" v-model="condition">
                                <span class="input-group-btn">
                                    <button class="btn btn-info" type="button" @click="getMembers('find')"></button>
                                </span>
                            </div>
                        </td>
                    </tr>
                </table>
            </div>
            <div style="height:635px;">
                <table class="table" style="margin:auto">
                    <thead>
                        <tr>
                            <td></td>
                            <td></td>
                            <td>/</td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td>
                                <button class="btn btn-success btn-sm" style="margin-left:15px;" data-toggle="modal"
                                    data-target="#memberModal" @click="clearMember();modalTitle = ''"></button>
                            </td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="member in memberList" :key="member.id">
                            <td>{{member.id}}</td>
                            <td>{{member.name}}</td>
                            <td>{{member.tel}}</td>
                            <td>{{member.sex==1?'':''}}</td>
                            <td>{{member.age}}</td>
                            <td>{{member.level==1?'':''}}</td>
                            <td>
                                <button class="btn btn-info btn-sm" data-toggle="modal" data-target="#memberModal"
                                    @click="getMemberById(member.id);modalTitle = ''"></button>
                                <button class="btn btn-danger btn-sm" @click="deleteMemberById(member.id)"></button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div style="margin-top:2px;">
                <div>{{allCount}}, {{allPage}}</div>
                <nav aria-label="Page navigation">
                    <ul class="pagination justify-content-center">
                        <li class="page-item" :class="{disabled: nowPage===1}">
                            <a href="#" aria-label="previous" class="page-link">
                                <span @click="nowPage--; getYachts();"></span>
                            </a>
                        </li>
                        <li v-for="page in allPage" :key="page" class="page-item" :class="{active: nowPage===page}"><a
                                href="#" class="page-link" @click="nowPage = page; getYachts();"
                                v-if="page>nowPage-5&&page<nowPage+5">{{page}}</a></li>
                        <li class="page-item" :class="{disabled: nowPage===allPage}">
                            <a href="#" aria-label="Next" class="page-link">
                                <span @click="nowPage++; getYachts();"></span>
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        </main>

        <!-- /Modal begin -->
        <div id="memberModal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title" id="myModalLabel">{{modalTitle}}</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span
                                aria-hidden="true">&times;</span></button>
                    </div>
                    <div class="modal-body">
                        <div class="form-group" v-if="modalTitle == ''">
                            <label>:</label>
                            <input disabled type="text" class="form-control" v-model="member.id">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <select class="form-control" v-model="member.level" :disabled="modalTitle == ''">
                                <option></option>
                                <option></option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label v-if="member.level == ''">:</label>
                            <label v-if="member.level == ''">:</label>
                            <input type="text" class="form-control" v-model="member.tel"
                                :disabled="modalTitle == ''" onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="text" class="form-control" v-model="member.name">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <div class="radio">
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="" v-model="member.sex">
                                </label>
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="" v-model="member.sex"
                                        style="margin-left:15px;">
                                </label>
                            </div>
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="number" min="0" class="form-control" v-model="member.age"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group" v-if="modalTitle == ''">
                            <label>:</label>
                            <input type="password" class="form-control" v-model="member.password"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group" v-if="modalTitle == ''">
                            <label>:</label>
                            <input type="password" class="form-control" v-model="confirmPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-success" @click="addMember"
                            v-if="modalTitle == ''"></button>
                        <button type="button" class="btn btn-primary" @click="updateMember"
                            v-if="modalTitle == ''"></button>
                    </div>
                </div>
            </div>
        </div>
        <!-- /Modal end -->
    </div>
</template>

<script>
    export default {
        name: 'adminMember',
        data() {
            return {
                member: { //
                    id: "",
                    name: "",
                    password: "",
                    tel: "",
                    sex: "",
                    age: "",
                    level: ""
                },
                confirmPassword: "",
                member_c: { //
                    id: "",
                    name: "",
                    tel: "",
                    sex: "--",
                    age: "",
                    level: "--"
                },
                memberList: [],
                condition: "",
                minAge: "",
                maxAge: "",
                nowPage: 1, //
                allPage: 0, //
                counts: 9, //
                allCount: 0, //,
                modalTitle: ""
            }
        },
        methods: {
            getMembers(button) {
                var sex = "";
                if (this.member_c.sex == "--") {
                    sex = "";
                } else if (this.member_c.sex == "") {
                    sex = 0;
                } else if (this.member_c.sex == "") {
                    sex = 1;
                }
                if (button == "find") { // "" , "" ,
                    this.nowPage = 1;
                }
                var level = "";
                if (this.member_c.level == "--") {
                    level = "";
                } else if (this.member_c.level == "") {
                    level = 0;
                } else if (this.member_c.level == "") {
                    level = 1;
                }
                this.axios({
                    method: "post",
                    url: "/api/member/admin/getMembers.action",
                    data: {
                        condition: this.condition,
                        minAge: this.minAge,
                        maxAge: this.maxAge,
                        sex,
                        level,
                        page: this.nowPage,
                        counts: this.counts
                    },
                    transformRequest: [function (data) { //post
                        // Do whatever you want to transform the data
                        let ret = '';
                        for (let it in data) {
                            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&';
                        }
                        return ret;
                    }],
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //
                        window.location.href = '/#/admin';
                    } else if (map.code == 200) {
                        if (map.data != null) {
                            var resultArr = JSON.parse(map
                                .data); //json,json[0]listjson,json[1]
                            this.allCount = resultArr[1]; //
                            this.allPage = parseInt(this.allCount / (this.counts + 1)) + 1; //
                            if (this.nowPage > this.allPage) { //,,
                                this.nowPage = 1;
                                this.getMembers();
                            } else {
                                this.memberList = JSON.parse(resultArr[0]); //
                            }
                        } else {
                            this.$emit("showAlert", true, "");
                        }
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //
                        window.location.href = '/#/admin';
                    }
                });
            },
            addMember() { //
                var confi = confirm("?");
                if (confi == true) {
                    var role = this.member.level;
                    if (!(/^[1][3,4,5,7,8][0-9]{9}$/.test(this.member.tel)) && role == "") {
                        this.$emit("showAlert", true, "");
                    } else if (this.member.name == "") {
                        this.$emit("showAlert", true, "");
                    } else if (!(/^\d+$/.test(this.member.tel)) && role == "") {
                        this.$emit("showAlert", true, "");
                    } else if (!(/^[0-9]+$/.test(this.member.age))) {
                        this.$emit("showAlert", true, "");
                    } else if (this.member.password.length < 6) {
                        this.$emit("showAlert", true, "");
                    } else if (this.confirmPassword != this.member.password) {
                        this.$emit("showAlert", true, "");
                    } else {
                        if (this.member.sex == "") {
                            this.member.sex = 0;
                        } else {
                            this.member.sex = 1;
                        }
                        if (role == "") {
                            this.member.level = 0;
                        } else if (role == "") {
                            this.member.level = 1;
                        }
                        this.axios({
                            url: "/api/member/admin/addMember.action",
                            data: this.member,
                            method: "post"
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) { //
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) { //
                                    $('#memberModal').modal('hide'); //
                                    this.clearMember();
                                    this.getMembers();
                                    this.$emit("showAlert", true, "");
                                } else {
                                    if (this.member.sex == 0) {
                                        this.member.sex = "";
                                    } else {
                                        this.member.sex = "";
                                    }
                                    if (this.member.level == 0) {
                                        this.member.level = "";
                                    } else if (this.member.level == 1) {
                                        this.member.level = "";
                                    }
                                    this.$emit("showAlert", true, map.data);
                                }
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //
                                window.location.href = '/#/admin';
                            }
                        });

                    }
                }
            },
            updateMember() {
                var confi = confirm("?");
                if (confi == true) {
                    var role = this.member.level;
                    if (this.member.name == "") {
                        this.$emit("showAlert", true, "");
                    } else if (!(/^[0-9]+$/.test(this.member.age))) {
                        this.$emit("showAlert", true, "");
                    } else {
                        if (this.member.sex == "") {
                            this.member.sex = 0;
                        } else {
                            this.member.sex = 1;
                        }
                        if (role == "") {
                            this.member.level = 0;
                        } else if (role == "") {
                            this.member.level = 1;
                        }
                        this.axios({
                            url: "/api/member/updateMemberMsg.action",
                            data: this.member,
                            method: "post"
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) { //
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) { //
                                    $('#memberModal').modal('hide'); //
                                    this.clearMember();
                                    this.getMembers();
                                    this.$emit("showAlert", true, "");
                                } else {
                                    this.$emit("showAlert", true, map.data);
                                }
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //
                                window.location.href = '/#/admin';
                            }
                        });

                    }
                }
            },
            getMemberById(memberId) {
                this.clearMember();
                if (memberId != "" && memberId != null) {
                    this.axios({
                        url: "/api/member/admin/getMemberById.action",
                        method: "get",
                        params: {
                            id: memberId
                        }
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) { //
                            window.location.href = '/#/admin';
                        } else if (map.code == 200) {
                            var data = JSON.parse(map.data);
                            if (data != null) {
                                this.member = data;
                                if (data.sex == 0) {
                                    this.member.sex = "";
                                } else {
                                    this.member.sex = "";
                                }
                                if (data.level == 0) {
                                    this.member.level = "";
                                } else {
                                    this.member.level = "";
                                }
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            deleteMemberById(memberId) {
                if (memberId != null) {
                    var confi = confirm("");
                    if (confi == true) {
                        this.axios({
                            url: "/api/member/admin/deleteMemberById.action",
                            params: {
                                id: memberId
                            },
                            method: "get"
                        }).then((response) => {
                            var map = response.data;
                            if (map.code == 401) { //
                                window.location.href = '/#/admin';
                            } else if (map.code == 200) {
                                if (map.data == null) {
                                    this.$emit("showAlert", true, "");
                                    this.getMembers();
                                } else {

                                    this.$emit("showAlert", true, map.data);
                                }
                            } else if (map.code == 400) { //

                                this.$emit("showAlert", true, "");
                            }
                        }).catch((error) => {
                            console.log(error);
                            if (error.response.status == 401) { //
                                window.location.href = '/#/admin';
                            }
                        });
                    }
                } else {
                    this.$emit("showAlert", true, "");
                }
            },
            clearMember() {
                this.member.id = "";
                this.member.name = "";
                this.member.password = "";
                this.confirmPassword = "";
                this.member.tel = "";
                this.member.sex = "";
                this.member.age = "";
                this.member.level = "";
            }
        },
        mounted() {
            this.getMembers();
        },
        created() {
            // this.$emit("ifAdmin");
        }
    }
</script>

<style scoped>

</style>